.Companies{
    box-sizing: border-box;
    
    /* Auto layout */
    
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;
    gap: 36px;
    
    position: relative;
    /* width: 837px; */
    width: 800px;
    height: 200px;
    /* left: 270px; */
    
    /* left: 50%;
    transform: translate(-50%); */

    
    
    border: 1px solid #000000;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.Companies-div-text{
    /* width: 600px; */
    height: 48px;
    
    font-family: 'Gilroy-SemiBold';
    font-size: 32px;
    line-height: 150%;
    /* identical to box height, or 48px */
    
    
    color: #ECECEC;
    
    border: 1px solid #000000;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    
    /* Inside auto layout */
    
    flex: none;
    order: 0;
    flex-grow: 0;
}

.Companies-div-images-div{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 0px;
    
    /* gap: 100px; */
    
    /* width: 814px; */
    width: 96%;
    /* height: 88px; */
    justify-content: space-between;
    
    
    /* Inside auto layout */
    
    flex: none;
    order: 1;
    flex-grow: 0;
}

.Company-img1{
    max-width: 191px;
    max-height: 73px;
    height: auto;
    width: auto;

    
    /* Inside auto layout */
    
    flex: none;
    order: 0;
    flex-grow: 0;
}

.Company-img2{
    /* width: 80px;
    height: 87px; */
    
    /* Inside auto layout */
    
    flex: none;
    order: 1;
    flex-grow: 0;
}

.Company-img3{
    /* width: 155px;
    height: 87.48px; */
        
    /* Inside auto layout */
    
    flex: none;
    order: 2;
    flex-grow: 0;
}

.Company-img4{
    /* width: 88px;
    height: 88px; */
    
    /* Inside auto layout */
    
    flex: none;
    order: 3;
    flex-grow: 0;
}

@media screen and (max-width: 1480px) {
    .Companies {
            width: 700px;
        }
}

@media screen and (max-width: 910px) {
    .Companies {
        width: 600px;

        border: none;
    }

    .Companies-div-text{
        border: none;
    }
}